<template>
    <div class="manage">
        <el-container>
            <el-header>
                <public-header @changeName="changeName" :show-input="true"></public-header>
            </el-header>
            <el-main>
                <data-table :name="name"></data-table>
            </el-main>
        </el-container>
    </div>
</template>

<script>
  import PublicHeader from '../components/Header'
  import DataTable from '../components/Table'
    export default {
        name: "Manage",
        components: {
            PublicHeader,
            DataTable
        },
        data() {
          return {
            name: ''
          }
        },
        methods: {
          changeName(val) {
            this.name = val
          }
        }
    }
</script>

<style scoped>
  .manage {
    max-width: 88%;
    margin: 0 auto;
  }
</style>



