<template>
  <div class="table">
    <el-table
      :data="data"
      border
      stripe
      style="width: 100%">
      <el-table-column
        fixed="left"
        prop="name"
        label="名称"
        width="180">
      </el-table-column>
      <el-table-column
        prop="description"
        label="简介"
        width="400">
      </el-table-column>
      <el-table-column
        prop="type"
        label="种类"
        width="120">
      </el-table-column>
      <el-table-column
        prop="price"
        sortable
        label="价格(元/kg)"
        width="140">
      </el-table-column>
      <el-table-column
        prop="saleAddress"
        label="有售地址"
        width="320">
      </el-table-column>
      <el-table-column
        prop="recommendLevel"
        sortable
        label="推荐度"
        width="220">
        <template slot-scope="scope">
          <el-rate v-model="scope.row.recommendLevel" disabled show-score text-color="#ff9900"></el-rate>
        </template>
      </el-table-column>
      <el-table-column
        prop="difficulty"
        label="获取难度"
        sortable
        width="220">
        <template slot-scope="scope">
          <el-rate v-model="scope.row.difficulty" disabled show-score text-color="#ff9900"></el-rate>
        </template>
      </el-table-column>
      <el-table-column
        prop="mainPictureUrl"
        label="主要图片"
        width="160">
        <template slot-scope="scope">
          <img :src="scope.row.mainPictureUrl" height="80"/>
        </template>
      </el-table-column>
      <el-table-column
        prop="otherPictureUrls"
        label="其他图片"
        width="480">
        <template slot-scope="scope">
          <img class="img" v-for="(item, index) of scope.row.otherPictureUrls" :key="index" :src="item" height="80"/>
        </template>
      </el-table-column>
      <el-table-column
        fixed="right"
        width="100">
        <template slot="header" slot-scope="scope">
          <el-button @click="add" type="primary" size="small">新增<i class="el-icon-plus el-icon--right"></i></el-button>
        </template>
        <template slot-scope="scope">
          <el-button @click="edit(scope.row)" type="primary" icon="el-icon-edit" circle size="small"></el-button>
          <el-button @click="remove(scope.row)" type="danger" icon="el-icon-delete" circle size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog :title="title" :visible.sync="dialogFormVisible" :close-on-click-modal="false" :before-close="beforeClose">
      <el-form :model="form" :rules="rules" ref="form" label-position="left" label-width="105px">
        <el-form-item label="名称" prop="name">
          <el-input v-model="form.name" placeholder="请输入名称(10个字符左右)"></el-input>
        </el-form-item>
        <el-form-item label="简介" prop="description">
          <el-input type="textarea" :rows="3" v-model="form.description" placeholder="请输入简介(100个字符左右)"></el-input>
        </el-form-item>
        <el-form-item label="种类" prop="type">
          <el-select v-model="form.type" placeholder="请选择种类">
            <el-option
              v-for="(item, index) in options"
              :key="index"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="价格(元/kg)" prop="price">
          <el-input-number v-model="form.price" :min="0" :step=".1" :precision="2"></el-input-number>
        </el-form-item>
        <el-form-item label="有售地址" prop="saleAddress">
          <el-input v-model="form.saleAddress" placeholder="请输入地址(50个字符以内)"></el-input>
        </el-form-item>
        <el-form-item label="推荐度" prop="recommendLevel">
          <el-rate v-model="form.recommendLevel" :allow-half="true" :show-score="true" text-color="#ff9900"></el-rate>
        </el-form-item>
        <el-form-item label="获取难度" prop="difficulty">
          <el-rate v-model="form.difficulty" :allow-half="true" :show-score="true" text-color="#ff9900"></el-rate>
        </el-form-item>
        <el-form-item label="主要图片" prop="mainPictureUrl">
          <el-upload
            action="/api/file/upload"
            :limit="1"
            :file-list="mainList"
            :on-success="mainListSuccess"
            :on-remove="mainListRemove"
            list-type="picture-card"
            :on-exceed="mainOverLimit">
            <i class="el-icon-plus"></i>
            <div slot="tip" class="el-upload__tip">只能上传1张jpg/png图片</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="其他图片" prop="otherPictureUrls">
          <el-upload
            action="/api/file/upload"
            :limit="5"
            :file-list="otherList"
            :on-success="otherListSuccess"
            :on-remove="otherListRemove"
            list-type="picture-card"
            :on-exceed="otherOverLimit">
            <i class="el-icon-plus"></i>
            <div slot="tip" class="el-upload__tip">最多上传5张jpg/png图片</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="confirm">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import axios from 'axios'
  import { mapState } from 'vuex'
    export default {
      name: "DataTable",
      props: {
        name: String
      },
      data() {
        let price = (rule, value, callback) => {
          if (value <= 0) {
            callback(new Error('请输入价格'))
          }
          callback()
        }
        let recommendLevel = (rule, value, callback) => {
          if (value <= 0 || value > 5) {
            callback(new Error('请选择推荐度'))
          }
          callback()
        }
        let difficulty = (rule, value, callback) => {
          if (value <= 0 || value > 5) {
            callback(new Error('请选择获取难度'))
          }
          callback()
        }
        let mainPictureUrl = (rule, value, callback) => {
          if (!value) {
            callback(new Error('请上传一张主要图片'))
          }
          callback()
        }
        let otherPictureUrls = (rule, value, callback) => {
          if (value.length === 0) {
            callback(new Error('请至少上传一张其他图片'))
          }
          callback()
        }
        return {
          tableData: [],
          dialogFormVisible: false,
          title: '',
          form: {
            id: '',
            name: '',
            description: '',
            type: '',
            price: '',
            saleAddress: '',
            recommendLevel: 0,
            difficulty: 0,
            mainPictureUrl: '',
            otherPictureUrls: []
          },
          mainList: [],
          otherList: [],
          rules: {
            name: [
              {required: true, message: '请输入名称', trigger: 'blur'}
            ],
            description: [
              {required: true, message: '请输入简介', trigger: 'blur'}
            ],
            type: [
              {required: true, message: '请选择种类', trigger: 'change'}
            ],
            price: [
              {required: true, validator: price, trigger: 'change'}
            ],
            saleAddress: [
              {required: true, message: '请输入地址', trigger: 'blur'}
            ],
            recommendLevel: [
              {required: true, validator: recommendLevel, trigger: 'change'}
            ],
            difficulty: [
              {required: true, validator: difficulty, trigger: 'change'}
            ],
            mainPictureUrl: [
              {required: true, validator: mainPictureUrl, trigger: 'change'}
            ],
            otherPictureUrls: [
              {type: 'array', required: true, validator: otherPictureUrls, trigger: 'change'}
            ]
          }
        }
      },
      computed: {
        data() {
          if (this.name) {
            let arr = []
            for (let i=0; i<this.tableData.length; i++) {
              if (this.tableData[i].name && this.tableData[i].name.indexOf(this.name) > -1) {
                arr.push(this.tableData[i])
              }
            }
            return arr
          } else {
            return this.tableData
          }
        },
        options() {
          let arr = []
          for (let i=0; i<this.type.length; i++) {
            arr.push(this.type[i].name)
          }
          return arr
        },
        ...mapState(['type'])
      },
      methods: {
        init() {
          this.form = {
            id: '',
            name: '',
            description: '',
            type: '',
            price: '',
            saleAddress: '',
            recommendLevel: 0,
            difficulty: 0,
            mainPictureUrl: '',
            otherPictureUrls: []
          }
          this.mainList = []
          this.otherList = []
        },
        add() {
          this.title = '新增'
          this.init()
          this.dialogFormVisible = true
        },
        edit(row) {
          this.title = '编辑'
          this.init()
          try {
            this.form = JSON.parse(JSON.stringify(row))
          } catch (e) {

          }
          this.mainList.push({name: '', url: row.mainPictureUrl})
          for (let i=0; row.otherPictureUrls && i<row.otherPictureUrls.length; i++) {
            let item = {name: '', url: row.otherPictureUrls[i]}
            this.otherList.push(item)
          }
          this.dialogFormVisible = true
        },
        remove(row) {
          this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.deleteProductInfo({id: row.id})
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除.'
            })
          })
        },
        deleteProductInfo(params) {
          axios.post('/api/product/deleteProductInfo', params)
            .then((res) => {
              const data = res.data
              if (data.code === 70000) {
                this.$message({
                  type: 'success',
                  message: '删除成功'
                })
                this.getHomePageInfo()
              }
            })
        },
        mainListSuccess(res, file, fileList) {
            this.mainList = []
            this.mainList.push(res.data)
        },
        mainListRemove(file, fileList) {
            this.mainList = []
        },
        otherListSuccess(res, file, fileList) {
            this.otherList.push(res.data)
        },
        otherListRemove(file, fileList) {
            let index = 0
            for (let i=0; this.otherList && i<this.otherList.length; i++) {
              if (this.otherList[i].url === file.url) {
                index = i
                break
              }
            }
            this.otherList.splice(index, 1)
        },
        mainOverLimit() {
          this.$message({
            type: 'warning',
            message: '只能上传一张图片!'
          })
        },
        otherOverLimit() {
          this.$message({
            type: 'warning',
            message: '最多上传五张图片!'
          })
        },
        cancel() {
          this.$confirm('取消将清空表单内容, 是否继续?')
            .then(() => {
              this.dialogFormVisible = false
              this.$refs.form.resetFields()
            }).catch(() => {

            })
        },
        confirm() {
          this.form.mainPictureUrl = this.mainList.length > 0 ? this.mainList[0].url : ''
          this.form.otherPictureUrls = []
          for (let i=0; i<this.otherList.length; i++) {
            this.form.otherPictureUrls.push(this.otherList[i].url)
          }
          this.$refs.form.validate((valid) => {
            if (valid) {
              if (this.title === '新增') {
                this.insertProductInfo(this.form)
              } else {
                this.updateProductInfo(this.form)
              }
            }
          })
        },
        insertProductInfo(params) {
          axios.post('/api/product/insertProductInfo', params)
            .then((res) => {
              const data = res.data
              if (data.code === 70000) {
                this.$message({
                  message: '新增成功',
                  type: 'success'
                });
                this.dialogFormVisible = false
                this.$refs.form.resetFields()
                this.getHomePageInfo()
              }
            })
        },
        updateProductInfo(params) {
          axios.post('/api/product/updateProductInfo', params)
            .then((res) => {
              const data = res.data
              if (data.code === 70000) {
                this.$message({
                  message: '修改成功',
                  type: 'success'
                });
                this.dialogFormVisible = false
                this.$refs.form.resetFields()
                this.getHomePageInfo()
              }
            })
        },
        beforeClose(done) {
          this.$confirm('退出将清空表单内容, 是否继续?')
            .then(() => {
              done()
              this.$refs.form.resetFields()
            }).catch(() => {

            })
        },
        getHomePageInfo(params) {
          params = params || {}
          axios.post('/api/product/getHomePageInfo', params)
            .then((res) => {
              const data = res.data
              if (data.code === 70000) {
                this.tableData = data.data.homePageInfos
              } else if (data.code === 70007) {
                this.$message({
                  message: '您还没有登录',
                  type: 'warning'
                });
                this.$router.push('login')
              }
            })
        }
      },
      mounted() {
        this.getHomePageInfo()
      }
    }
</script>

<style scoped>
.el-rate {
  margin-top: 10px;
}
.img {
  margin-right: 10px;
}
.img:last-child {
  margin-right: 0;
}
</style>



