<template>
    <el-row type="flex" justify="space-between" class="header">
        <el-col :span="12">
            <div class="title">青海藏族特产管理系统</div>
        </el-col>
        <el-col :span="6" v-if="showInput">
          <el-input
            clearable
            size="medium"
            placeholder="请输入内容"
            prefix-icon="el-icon-search"
            v-model="name">
          </el-input>
        </el-col>
        <el-col :span="6" style="text-align: right">
            <div class="user" v-if="userName">
              Hello, <span class="name">{{userName}}</span>
              <span class="logout" @click="logout">[ 注销 ]</span>
            </div>
            <div class="login" v-else>
              <span class="goLogin" @click="goLogin">[ 登录 ]</span>
            </div>
        </el-col>
    </el-row>
</template>

<script>
  import axios from 'axios'
  import { mapState, mapActions } from 'vuex'
    export default {
        name: "PublicHeader",
        props: {
          showInput: Boolean
        },
        data() {
          return {
            name: ''
          }
        },
        computed: {
          ...mapState(['userName'])
        },
        watch : {
          name(val) {
            this.$emit('changeName', val)
          }
        },
        methods: {
          goLogin() {
            this.$router.push('login')
          },
          logout() {
            this.$confirm('是否退出登录?')
              .then(() => {
                this.getLogout()
              }).catch(() => {

            })
          },
          getLogout() {
            axios.post('/api/user/logout', {})
              .then((res) => {
                const data = res.data
                if (data.code === 70000) {
                  this.$message({
                    message: '您已注销',
                    type: 'success'
                  });
                  this.setUser('')
                  this.$router.push('login')
                }
              })
          },
          ...mapActions(['setUser'])
        }
    }
</script>

<style scoped>
.header {
    line-height: 60px;
}
.title {
    font-size: 28px;
    font-weight: bold;
    color: #67C23A;
}
.user {
    font-size: 16px;
    color: #666;
}
.name {
    color: #409EFF;
}
.logout {
    color: #F56C6C;
    cursor: pointer;
}
.login {
    font-size: 14px;
    color: #409EFF;
}
.goLogin {
    cursor: pointer;
}
</style>



