<template>
  <div class="waterfall" id="waterfall" v-loading.fullscreen.lock="loading">
    <div class="item" v-for="(item, index) of data" :key="index" ref="item">
      <data-card :cardData="item" :idx="index"></data-card>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import DataCard from './Card'
  import imagesloaded from 'imagesloaded'
  export default {
        name: "DataWaterfall",
        components: {
            DataCard
        },
        data() {
          return {
            data: [],
            timer: null,
            loading: false,
            pageInfo: {
              page: 1,
              count: 12
            },
            more: true
          }
        },
        methods: {
          getPosition() {
            const top = 80
            const left = ['6%', '28%', '50%', '72%']
            let items = this.$refs.item
            if (!items) {
              return
            }
            let length = items.length
            if (length > 0) {
              items[0].style.left = left[0]
              items[0].style.top = top + 'px'
            }
            if (length > 1) {
              items[1].style.left = left[1]
              items[1].style.top = top + 'px'
            }
            if (length > 2) {
              items[2].style.left = left[2]
              items[2].style.top = top + 'px'
            }
            if (length > 3) {
              items[3].style.left = left[3]
              items[3].style.top = top + 'px'
            }
            if (length > 4) {
              let heights = []
              for (let i=0; i<4; i++) {
                heights.push(items[i].clientHeight)
              }
              for (let i = 4; i < length; i++) {
                let min = Math.min.apply(null, heights)
                let index = heights.indexOf(min)
                items[i].style.left = left[index]
                items[i].style.top = heights[index] + top + 'px'
                heights[index] += items[i].clientHeight
              }
            }
          },
          getScroll() {
            if (this.timer) {
              clearTimeout(this.timer)
            }
            this.timer = setTimeout(() => {
              let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
              let scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight
              let clientHeight = document.documentElement.clientHeight || document.body.clientHeight
              if (scrollTop + clientHeight >= scrollHeight) {
                if (this.more) {
                  this.getHomePageInfo(this.pageInfo)
                }
              }
            }, 1000)
          },
          getHomePageInfo(params) {
            params = params || {}
            axios.post('/api/product/getHomePageInfo', params)
              .then((res) => {
                const data = res.data
                if (data.code === 70000) {
                  this.data = this.data.concat(data.data.homePageInfos)
                  if (this.data.length === data.data.count || data.data.homePageInfos.length === 0) {
                    this.more = false
                  } else {
                    this.pageInfo.page ++
                  }
                } else if (data.code === 70007) {
                  this.$message({
                    message: '您还没有登录',
                    type: 'warning'
                  });
                  this.$router.push('login')
                }
              })
          }
        },
        mounted() {
          this.loading = true
          this.getHomePageInfo(this.pageInfo)
          window.addEventListener('scroll', this.getScroll)
          imagesloaded('#waterfall', () => {
            this.getPosition()
            this.loading = false
          })
        },
        updated() {
          imagesloaded('#waterfall', () => {
            this.getPosition()
          })
        }
    }
</script>

<style scoped>
.item {
  position: absolute;
  top: -1000px;
  left: -1000px;
  width: 22%;
  padding: 0 10px;
  box-sizing: border-box;
}
</style>



