<template>
  <div class="register">
    <el-card shadow="always">
      <el-form :model="registerForm" :rules="registerRules" ref="register" label-width="80px">
        <el-form-item label="用户名" prop="userName" style="margin-top: 20px">
          <el-input v-model="registerForm.userName" placeholder="请输入用户名(8~10个字符)" clearable></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" v-model="registerForm.password" placeholder="请输入密码(6~8个字符)" clearable></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="pwd">
          <el-input type="password" v-model="registerForm.pwd" placeholder="请确认密码(6~8个字符)" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="text" @click="goLogin">已有账号?去登录</el-button>
          <el-button type="primary" size="small" @click="register">注 册</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
  import axios from 'axios'
    export default {
      name: "Register",
      data() {
        let password = (rule, value, callback) => {
          if (!value) {
            callback(new Error('密码不能为空'))
          } else if (value.length < 6 || value.length > 8) {
            callback(new Error('长度在 6 到 8 个字符'))
          } else {
            if (this.registerForm.pwd !== '') {
              this.$refs.register.validateField('pwd')
            }
            callback()
          }
        }
        let pwd = (rule, value, callback) => {
          if (!value) {
            callback(new Error('密码不能为空'))
          } else if (value !== this.registerForm.password) {
            callback(new Error('两次输入密码不一致'))
          } else {
            callback()
          }
        }
        return {
          registerForm: {
            userName: '',
            password: '',
            pwd: ''
          },
          registerRules: {
            userName: [
              {required: true, message: '用户名不能为空', trigger: 'blur'},
              {min: 8, max: 10, message: '长度在 8 到 10 个字符', trigger: 'blur'}
            ],
            password: [
              {required: true, validator: password, trigger: 'blur'}
            ],
            pwd: [
              {required: true, validator: pwd, trigger: 'blur'}
            ]
          }
        }
      },
      methods: {
        goLogin() {
          this.$confirm('此操作将清空表单内容, 是否继续?')
            .then(() => {
              this.$router.push('login')
            }).catch(() => {

          })
        },
        register() {
          this.$refs.register.validate((valid) => {
            if (valid) {
              this.getRegister(this.registerForm)
            }
          })
        },
        getRegister(params) {
          axios.post('/api/user/register', params)
            .then((res) => {
              const data = res.data
              if (data.code === 70000) {
                this.$message({
                  message: '注册成功',
                  type: 'success'
                });
                this.$router.push('login')
              } else {
                this.$message({
                  message: data.msg,
                  type: 'error'
                });
              }
            })
        }
      }
    }
</script>

<style scoped>
  .register {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background-image: url("../assets/img/bg.jpg");
    background-size: 100% 100%;
  }
  .el-card {
    position: absolute;
    left: 50%;
    top: 35%;
    transform: translate(-50%, -50%);
    width: 400px;
  }
</style>



