<template>
  <div class="card">
    <el-card slot="reference" :body-style="{ padding: '0px' }" shadow="hover">
      <img class="img" :preview="idx" :src="cardData.mainPictureUrl">
      <img v-show="false" v-for="(item, index) of cardData.otherPictureUrls" :key="index" :preview="idx" :large="item">
      <div class="more">
        <i class="el-icon-picture"></i> {{cardData.otherPictureUrls && cardData.otherPictureUrls.length + 1}}
      </div>
      <div class="name-and-price">
        <div class="name"><b>{{cardData.name}}</b></div>
        <div class="price"><b style="color: #ff9900;">{{cardData.price}}</b> (元/kg)</div>
        <div class="type">{{cardData.type}}</div>
      </div>
      <div class="rate">
        <div class="recommend">
          <b style="flex: 1;">推荐度：</b>
          <el-rate
            v-model="cardData.recommendLevel"
            disabled
            show-score
            text-color="#ff9900">
          </el-rate>
        </div>
        <div class="difficulty">
          <b style="flex: 1;">获取难度：</b>
          <el-rate
            v-model="cardData.difficulty"
            disabled
            show-score
            text-color="#ff9900">
          </el-rate>
        </div>
      </div>
      <div class="address">
        <b>有售地址：</b>{{cardData.saleAddress}}
      </div>
      <div class="desc">
        <b>简介：</b>{{cardData.description}}
      </div>
    </el-card>
  </div>
</template>

<script>
    export default {
        name: "DataCard",
        props: {
          cardData: Object,
          idx: Number
        }
    }
</script>

<style scoped>
.el-card {
  position: relative;
  margin-bottom: 20px;
  color: #333;
}
.img {
  width: 100%;
  cursor: pointer;
}
.more {
  display: inline-block;
  position: absolute;
  right: 5px;
  top: 5px;
  padding: 3px 8px;
  border-radius: 4px;
  font-size: 13px;
  text-align: center;
  background-color: rgba(0,0,0,0.2);
  color: #eee;
  user-select: none;
}
.name-and-price {
  position: relative;
  display: flex;
  margin: 0 10px;
  padding: 10px 0 5px 0;
  height: 20px;
  line-height: 20px;
  border-bottom: 1px solid #eee;
}
.type {
  position: absolute;
  left: 0;
  top: -30px;
  font-size: 12px;
  color: #eee;
  user-select: none;
}
.name {
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding-right: 20px;
}
.price {
  font-size: 13px;
  text-align: right;
}
.rate {
  margin: 0 10px;
  padding: 5px 0;
  height: 40px;
  font-size: 12px;
  color: #666;
  border-bottom: 1px solid #eee;
}
.recommend {
  display: flex;
  height: 20px;
  line-height: 20px;
}
.difficulty {
  display: flex;
  height: 20px;
  line-height: 20px;
}
.address {
  margin: 0 10px;
  padding: 5px 0;
  line-height: 1.5;
  font-size: 12px;
  color: #666;
  border-bottom: 1px solid #eee;
}
.desc {
  margin: 0 10px;
  padding: 5px 0 10px 0;
  line-height: 1.5;
  font-size: 12px;
  color: #666;
}
</style>



