import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)

let type = []
try {
  if (localStorage.getItem('productType')) {
    type = JSON.parse(localStorage.getItem('productType'))
  } else {
    axios.post('/api/product/queryProductType', {})
      .then((res) => {
        const data = res.data
        if (data.code === 70000) {
          type = data.data
          localStorage.setItem('productType', JSON.stringify(type))
        }
      })
  }
} catch (e) {

}

let userName = ''
if (sessionStorage.getItem('userName')) {
  userName = sessionStorage.getItem('userName')
}

export default new Vuex.Store({
  state: {
    userName: userName, // 当前登录用户
    type: type // 土特产种类
  },
  actions: {
    setUser (ctx, userName) {
      ctx.commit('setUser', userName)
    }
  },
  mutations: {
    setUser (state, userName) {
      state.userName = userName
      if (userName) {
        sessionStorage.setItem('userName', userName)
      } else {
        sessionStorage.removeItem('userName')
      }
    }
  }
})



