<template>
  <div class="login">
    <el-card shadow="always">
      <el-form :model="loginForm" :rules="loginRules" ref="login" label-width="75px">
        <el-form-item label="用户名" prop="userName" style="margin-top: 20px">
          <el-input v-model="loginForm.userName" placeholder="请输入用户名" clearable></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" v-model="loginForm.password" placeholder="请输入密码" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="text" @click="goRegister">没有账号?去注册</el-button>
          <el-button type="primary" size="small" @click="login">登 录</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
  import axios from 'axios'
  import { mapActions } from 'vuex'
    export default {
      name: "Login",
      data() {
        return {
          loginForm: {
            userName: '',
            password: ''
          },
          loginRules: {
            userName: [
              {required: true, message: '用户名不能为空', trigger: 'blur'}
            ],
            password: [
              {required: true, message: '密码不能为空', trigger: 'blur'}
            ]
          }
        }
      },
      methods: {
        goRegister() {
          this.$router.push('register')
        },
        login() {
          this.$refs.login.validate((valid) => {
            if (valid) {
              this.getLogin(this.loginForm)
            }
          })
        },
        getLogin(params) {
          axios.post('/api/user/login', params)
            .then((res) => {
              const data = res.data
              if (data.code === 70000) {
                this.$message({
                  message: '登录成功',
                  type: 'success'
                });
                this.setUser(params.userName)
                this.$router.push('manage')
              } else {
                this.$message({
                  message: data.msg,
                  type: 'error'
                });
              }
            })
        },
        ...mapActions(['setUser'])
      }
    }
</script>

<style scoped>
  .login {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background-image: url("../assets/img/bg.jpg");
    background-size: 100% 100%;
  }
  .el-card {
    position: absolute;
    left: 50%;
    top: 35%;
    transform: translate(-50%, -50%);
    width: 400px;
  }
</style>



