<template>
    <div class="home">
      <el-container>
        <el-header>
          <public-header :show-input="false"></public-header>
        </el-header>
        <el-main>
          <data-waterfall></data-waterfall>
        </el-main>
      </el-container>
    </div>
</template>

<script>
  import PublicHeader from '../components/Header'
  import DataWaterfall from '../components/Waterfall'
  export default {
        name: "Home",
        components: {
          PublicHeader,
          DataWaterfall
        }
    }
</script>

<style scoped>
  .home {
    max-width: 88%;
    margin: 0 auto;
  }
</style>



